
* {
    box-sizing: border-box;
}

body {
    font-family: sans-serif; 
    margin: 0 auto;
    overflow-x: hidden;
}

:root {
    --margin_bottom: 30px;
    --box_shadow: 1px 2px 1px silver;
    --background_color: rgb(245, 243, 243); 
}

/* FADE IN BODY */

.fade_transition {
    opacity: 0;
    transition-property: opacity;
    transition-duration: 2s;
}

.fade_in {
    opacity: 1;
}

.toggle_opacity {
    opacity: 0; 
}

/* SLIDE IN EFFECT ON LOAD */ 
.slide_transition {
    position: relative;
    transition-property: right, left;
    transition-duration: 2s;
}

.slide_out {
    right: 2000px;
}

.slide_in {
    right: 0px;
}

.slide_out_left {
    left: 2000px;
}

.slide_in_right {
    left: 0px;
}

/* PAGE TRANSITIONS */ 

.page_opacity {
    opacity: 0;
}


